@import url(http://fonts.googleapis.com/earlyaccess/notosanskr.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat:700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

html {
  width: 100vw;
  height: auto;
  overflow-x: hidden;
}

body {
  overflow-x: hidden;
  font-family: "Noto Sans KR", sans-serif;
}

a {
  color: white;
  text-decoration: none;
}

#sectionAbout {
  padding-top: 40px;
  padding-left: 20px;

  border-top: 5px solid #f5f5f5;
  background-color: #f5f5f5;
}

.myImage {
  width: 20%;
}

.aboutMe {
  margin-left: 100px;
  width: 100%;
  margin-bottom: 60px;
}

.myProfile {
  display: flex;
  flex: space-between 1;
}

@media (max-width: 1000px) {
  #sectionAbout {
    padding: 0px;
  }
  .myProfile {
    display: block;
  }
  .myImage {
    width: 100%;
    text-align: center;
  }
  .aboutMe {
    text-align: center;
    margin: 0;
  }
}

.container {
  display: flex;
  justify-content: space-between;
  background-color: rgb(192, 192, 192);
}

.leftMenu {
  position: relative;
  margin-left: 5rem;
}
.nuJooy {
  position: absolute;
  left: 50%;
  top: 10%;
  font-size: 30px;
  color: white;
}
.rightMenu {
}
@media (max-width: 1000px) {
  .rightMenu ul li {
    display: none;
  }
}
li {
  list-style: none;
  display: inline;
  margin-right: 2rem;
  font-size: 20px;
  color: white;
}
li:hover {
  border-bottom: 2px solid white;
  cursor: pointer;
}

* {
  scroll-behavior: smooth;
  max-width: 100%;
  height: auto;
}

.hamburgerMenu {
  width: 50px;
  display: none;
  margin-right: 20px;
  cursor: pointer;
  max-width: 100%;
  height: auto;
}
@media (max-width: 1000px) {
  .hamburgerMenu {
    display: block;
  }
}

@media (max-width: 300px) {
  .hamburgerMenu {
    display: block;
  }
}

.mobileMenu {
  width: 100%;
  height: 300px;
  background-color: rgba(192, 192, 192);
}
@media (min-width: 1000px) {
  .mobileMenu {
    display: none;
  }
}

#fly-in {
  font-size: 2em;
  /* margin-left: 150px; */
  margin-bottom: 200px;
  height: 410px;
  text-align: center;
}
@media (max-width: 1000px) {
  #fly-in {
    /* margin-left: 100px; */
    margin-bottom: 100px;
  }
}

@media (max-width: 700px) {
  #fly-in {
    /* margin-left: 60px; */
    margin-bottom: 100px;
  }
}

#fly-in span {
  display: block;
  font-size: 0.4em;
  opacity: 0.8;
}

#fly-in div {
  margin: 3vh 0;
  opacity: 0;
  left: 10vw;
  width: 80vw;
  -webkit-animation: switch 15s linear infinite;
          animation: switch 15s linear infinite;
  justify-content: center;
  display: flex;
}

#fly-in div:nth-child(2) {
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
}
#fly-in div:nth-child(3) {
  -webkit-animation-delay: 7s;
          animation-delay: 7s;
}
#fly-in div:nth-child(4) {
  -webkit-animation-delay: 10s;
          animation-delay: 10s;
}

@-webkit-keyframes switch {
  0% {
    opacity: 0;
    -webkit-filter: blur(20px);
            filter: blur(20px);
    -webkit-transform: scale(3);
            transform: scale(3);
  }
  3% {
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  10% {
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  13% {
    opacity: 0;
    -webkit-filter: blur(10px);
            filter: blur(10px);
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes switch {
  0% {
    opacity: 0;
    -webkit-filter: blur(20px);
            filter: blur(20px);
    -webkit-transform: scale(3);
            transform: scale(3);
  }
  3% {
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  10% {
    opacity: 1;
    -webkit-filter: blur(0);
            filter: blur(0);
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
  13% {
    opacity: 0;
    -webkit-filter: blur(10px);
            filter: blur(10px);
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.hoverImg {
  -webkit-transform: scale(1);
  transform: scale(1);
  transition: all 0.3s ease-in-out;
  display: inline;
  margin: 30px;
}

.hoverImg:hover {
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}

.hoverImg {
  overflow: hidden;
}

#foot {
  border: none;
  border-radius: 15px;
  box-shadow: 0 0 8px rgb(207, 207, 207);
}

button:hover {
  border: none;
  padding: 15px;
  border-radius: 15px;
  box-shadow: inset 0 0 8px #f9f8fc;
}

#foot {
  position: relative;
  top: 15px;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border: none;
  padding: 15px;
  border-radius: 15px;
  box-shadow: 0 0 8px #d9d4e7;
  width: 200px;
  height: 65px;
  background-color: #d9d4e7;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
}

.button-os {
  position: absolute;
  width: 200px;
  height: 55px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  border-radius: 10px, 10px, 10px, 10px;
  background-color: #f9f8fc;
  border: 2.5px solid palevioletred;
  transition: background-color 2.2s;
  transition-duration: 3s;
  transition: 1.5s;
}

.button-os:hover {
  position: absolute;
  top: 30px;
  background-color: #fec7d7;
}

.button-os a {
  font-size: 20px;
  display: block;
  text-decoration: none;
  color: grey;
  transition: 1.5s;
  font-size: 20px;
}

.button-os a:hover {
  letter-spacing: 5px;
  color: white;
}

